@import '~antd/lib/style/themes/default.less';
@import '~antd/dist/antd.less'; // Import Ant Design styles by less entry

// Rounded corners
@border-radius-base: 15px; // major border radius

.rouded-corners{
    border-radius: 15px
}

.ant-card-actions{
    border-radius: 0 0 15px 15px;
}

.slick-list{
    border-radius: 15px;
}

// navbar
.ant-layout-header{
    border-bottom: 0;
}

// footer
.ant-layout-footer{
    background-color: #080808;
    color: white;
    padding-top: 40px;
}