@import '~antd/dist/antd.less';

.ant-layout-footer {
	background-color: #080808;
	color: #ffffff;
	padding-top: 40px;
	text-align: center;
	
	h1, h2, h3, h4, h5, h6 {
		color: #ffffff;
		text-transform: uppercase;
	}

	.logo {
		max-height:100px;
		max-width: max(15%, 200px);
		margin: 0 0 40px 0;
	}

	.block {
		margin: 0 0 40px 0;
	}

	a{
		margin:0 1em 0 1em;
	}
}

@media(min-width: 767px) {
	.ant-layout-footer {
		padding-left: 20vh;
		padding-right: 20vh;
	}
}